h: 100%;\n}\n\n@mixin full-width-generator($suffix) {\n  [#{$prefix}~='full-width-until\\@#{$suffix}'] {\n    width: 100% !important;\n    max-width: 100% !important;\n  }\n}\n\n@media (max-width: $sm-break) {\n  @include full-width-generator('sm');\n}\n\n@media (max-width: $md-break) {\n  @include full-width-generator('md');\n}\n\n@media (max-width: $lg-break) {\n  @include full-width-generator('lg');\n}\n\n@media (max-width: $xl-break) {\n  @include full-width-generator('xl');\n}\n","@each $spacing-suffix, $spacing-value in $spacing-map {\n  @each $rule in margin, padding {\n    @each $location-suffix in $location-suffixes {\n      [#{$prefix}~='#{$rule}#{$location-suffix}#{$spacing-suffix}'] {\n        #{$rule}#{$location-suffix}: $spacing-value !important;\n      }\n    }\n  }\n}\n","/* ------------------------------------ *\\\n    $FONTS\n\\* ------------------------------------ */\n@import url(\"https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,400;0,700;1,400;1,700&display=swap\");\n@import url('https://fonts.googleapis.com/css2?family=Oswald:wght@400;500;700&display=swap');\n\n@font-face {\n  font-family: 'Go Bold';\n  src: url(\"../fonts/gobold_regular_italic-webfont.woff2\") format(\"woff2\"), url(\"../fonts/gobold_regular_italic-webfont.woff\") format(\"woff\");\n  font-weight: normal;\n  font-style: normal;\n}\n","/* ------------------------------------ *\\\n    $FORMS\n\\* ------------------------------------ */\n\nform ol,\nform ul {\n  list-style: none;\n  margin-left: 0;\n}\n\nlegend {\n  margin-bottom: 6px;\n  font-weight: bold;\n}\n\nfieldset {\n  border: 0;\n  padding: 0;\n  margin: 0;\n  min-width: 0;\n}\n\ninput,\nselect,\ntextarea {\n  width: 100%;\n  border: none;\n  appearance: none;\n}\n\ninput[type=text],\ninput[type=password],\ninput[type=email],\ninput[type=search],\ninput[type=tel],\ninput[type=\"number\"],\nselect {\n  font-size: 16px;\n  font-family: $ff-font;\n  padding: $space-half $space;\n  box-shadow: none;\n  border: $b